<template>
      <main>
        <div class="col-6">
          <div class="row">
            <h3>Third Party Updates - Verfügbar: {{ infos.length }}</h3>
            <hr>
            <table class="table table-striped table-bordered table-responsive" id="myTable" style="width: 45%;">
                <thead>
                  <tr>
                    <th>ID</th>
                    <th>Hostname</th>
                    <th>Software</th>
                    <th>Installierte Version</th>
                    <th>Verfügbare Version</th>
                    <th>Gewichtung</th>
                  </tr>
                </thead>
                <tbody>
                <tr v-for="u in infos">
                  <th>{{u.id}}</th>
                  <th>{{u.Hostname}}</th>
                  <th>{{u.Software}}</th>
                  <th>{{u.LocalVersion}}</th>
                  <th>{{u.UpgradeVersion}}</th>
                  <th v-bind:class="{ 'text-warning': u.Weight > 10, 'text-danger': u.Weight > 100 }">{{u.Weight}}</th>
                </tr>
                </tbody>
              </table>
          </div>
          <div class="row">
                <button class="btn btn-secondary" style="float: right; " @click="fetchChocoData">Neu laden</button>
            </div>  
        </div>
        <div class="col-6">
            <div class="row">
            <h3>Windows Updates - Verfügbar: {{ wsuses.length }}</h3>
            <hr>
            <table class="table table-striped table-bordered table-responsive" id="myTablewsus" style="width: 45%;">
                <thead>
                  <tr>
                    <th>ID</th>
                    <th>Hostname</th>
                    <th>OS</th>
                    <th>IpAddress</th>
                    <th>UpdateTitle</th>
                    <th>LegacyName</th>
                    <th>SecurityBulletins</th>
                    <th>UpdateInstallationStatus</th>
                    <th>UpdateApprovalAction</th>
                    <th>CreationDate</th>
                  </tr>
                </thead>
                <tbody>
                <tr v-for="w in wsuses">
                  <th>{{w.id}}</th>
                  <th>{{w.Computername}}</th>
                  <th>{{w.OS}}</th>
                  <th>{{w.IpAddress}}</th>
                  <th>{{w.UpdateTitle}}</th>
                  <th>{{w.LegacyName}}</th>
                  <th>{{w.SecurityBulletins}}</th>
                  <th>{{w.UpdateInstallationStatus}}</th>
                  <th>{{w.UpdateApprovalAction}}</th>
                  <th>{{w.CreationDate}}</th>
                </tr>
                </tbody>
              </table>
              </div>
              <div class="row">
                <button class="btn btn-secondary" style="float: right; " @click="fetchWSUSData">Neu laden</button>
            </div>   
        </div>
      </main>
</template>

<script>
export default {
  data() {
    return {
      info: {
        id: '',
        Hostname: '',
        Software: '',
        LocalVersion: '',
        UpgradeVersion: '',
        Weight: ''
      },
      infos: [],
      wsus: {
        id: '',
        UpdateTitle: '',
        LegacyName: '',
        SecurityBulletins: '',
        Computername: '',
        OS: '',
        IpAddress: '',
        UpdateInstallationStatus: '',
        UpdateApprovalAction: '',
        CreationDate: ''
      },
      wsuses: []
    };
  },
  methods: {
    submit() {
      console.log(this.user);
    },
    fetchChocoData() {
      this.$http.get('http://localhost:8090/upgrades')
          .then(response => {
            return response.json();
          })
          .then(data => {
            const resultArray = [];
            for (let key in data) {
              resultArray.push(data[key]);
            }
            this.infos = resultArray;
            sleep(500).then(() => {
                $('#myTable').DataTable();
            });
          });
      },
      fetchWSUSData() {
      this.$http.get('http://localhost:8095/')
          .then(response => {
            return response.json();
          })
          .then(data => {
            const resultArray = [];
            for (let key in data) {
              resultArray.push(data[key]);
            }
            this.wsuses = resultArray;
            sleep(500).then(() => {
                $('#myTablewsus').DataTable();
            });
          });
      }
  }
}

function sleep (time) {
  return new Promise((resolve) => setTimeout(resolve, time));
}
</script>

<style>
.dataTables_wrapper {
  width: 100%;
}
</style>


